<template>
  <div>
    
    <Header/>
    <Addtodo v-on:addtodo="addtodo" />
    <Todos v-on:completed="completeTodo" v-on:del-todo="deleteTodo" v-bind:todos="todos"/>
  </div>
</template>

<script>

import Addtodo from './components/layout/Addtodo'
import Header from './components/layout/Header'
import Todos from './components/Todos'



export default {

  name: 'App',
  components: {
    Todos,
    Header,
    Addtodo
},
  data(){
    return {
      todos: [
      ]
    }
    
   
  },
  methods: {
    deleteTodo(id){
      this.todos = this.todos.filter(todo => todo.id !== id)
    },
    completeTodo(id)
    {
      this.todos.filter(todo => todo.id == id)[0].completed =  !this.todos.filter(todo => todo.id == id)[0].completed
    },
    addtodo(todo){
      todo.id = this.todos.length + 1
      this.todos.push(todo)
    }
  }
}
</script>

