<template>
    <div class="form">
        <form  @submit="addtodo" action="#">
            <input autocomplete="off" :class="{'empty': !empty}" v-model="title" type="text" placeholder="Add Todo..." name="title" >
           <button type="submit" class="btn">Add</button>
        </form>
    </div>
</template>


<script>


export default{
    name: 'Addtodo-component',
    data(){
        return{ 
            title:'',
            empty:true,
        }
    },
    methods:
    {
        addtodo(e){

            e.preventDefault()

            if(this.title !== '')
            {
                let newtodo = {
                id: null,
                title: this.title,
                completed:false
                }

                this.$emit('addtodo', newtodo)
                return
            }
            this.empty = false

            

        }
    }
}
</script>


