    .form 
    {
        max-width: 300px;
        width:100%;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin: 0 1rem;
        height: 40px;
        align-items: center;
        
    }
    .form input.empty
    {
        border: 2px solid red;
        animation:shake 300ms;
        
    }

@keyframes shake {
    25%
    {
        transform: translate(4px);
    }
    50%{
        transform: translate(-4px);
    }
    75%{
        transform: translate(4px);
    }
}
    form 
    {
