<template>
    <div class="todo-item">

        <div class="title">
            <div  :class="{'is_complete':todo.completed}"></div>
            <p>{{todo.title}}</p>
        </div>
        <input v-if="todo.completed" checked type="checkbox"  v-on:change="markComplete" />
        <input v-if="todo.completed != true"  type="checkbox"  v-on:change="markComplete" />
        <button @click="$emit('del-todo', todo.id)" class="del">del</button>
    </div>
</template>

<script>
    export default{
        name: 'Todoitem-component',
        props:["todo"],
        methods: 
        {
            markComplete(){
               this.$emit('completed', this.todo.id)
            }
        }


    }
</script>

